<template>
  <div class="hello">
    <div id="align_center" class="w-50">
      <b-spinner v-if="show_spinner" class="mb-3" variant="primary" />
      <b-alert v-if="show_success" show variant="primary">
        <a href="/" class="alert-link"
          >Submitted - Click to redirect to homepage
        </a>
      </b-alert>
      <b-alert v-if="show_failed" show variant="danger"
        >Failed - Try again.</b-alert
      >
    </div>

    <div id="align_center" class="w-50 shadow-sm">
      <b-card bg-variant="light">
        <b-form @submit="onSubmit" @reset="onReset" v-if="show">
          <b-form-group
            label-cols-lg="3"
            label="Contact"
            label-size="lg"
            label-class="font-weight-bold pt-0"
            class="mb-0"
          >
            <b-form-group
              id="email"
              label-cols-sm="3"
              label="Email"
              description="We'll never share your email with anyone else."
              label-align-sm="right"
              label-for="email"
              type="email"
              required
            >
              <b-form-input
                id="email"
                v-model="form.email"
                placeholder="Enter email"
              />
            </b-form-group>

            <b-form-group
              id="name"
              label-cols-sm="3"
              label="Email"
              description="Let us know your name."
              label-align-sm="right"
              label-for="name"
              type="text"
              required
            >
              <b-form-input
                id="name"
                v-model="form.name"
                placeholder="Enter name"
              />
            </b-form-group>

            <b-form-group
              id="category"
              label-cols-sm="3"
              label="Category"
              label-align-sm="right"
              label-for="category"
              type="text"
              required
            >
              <b-form-select
                id="category"
                v-model="form.category"
                :options="categories"
              />
            </b-form-group>

            <b-form-group
              id="comment"
              label-cols-sm="3"
              label="Comment"
              label-align-sm="right"
              label-for="comment"
              type="textarea"
              required
            >
              <b-form-textarea
                id="inputHorizontal"
                v-model="form.comment"
                rows="3"
                max-rows="6"
                placeholder="Anything you want to let us know."
              />
            </b-form-group>

            <b-row class="mb-2 justify-content-md-center">
              <b-button-toolbar>
                <b-button-group class="mx-1">
                  <b-button type="submit" variant="primary">Submit</b-button>
                </b-button-group>
                <b-button-group class="mx-1">
                  <b-button type="reset" variant="danger">Reset</b-button>
                </b-button-group>
              </b-button-toolbar>
            </b-row>
          </b-form-group>
        </b-form>
      </b-card>
    </div>
  </div>
</template>

<script>
"use strict";

export default {
  name: "Contact",

  data() {
    return {
      form: {
        email: "",
        name: "",
        comment: "",
        category: null,
        checked: []
      },
      categories: [{ text: "Select One", value: null }, "A", "B", "C", "D"],
      show: true,

      show_spinner: false,
      show_success: false,
      show_failed: false
    };
  },

  methods: {
    onSubmit(evt) {
      evt.preventDefault();

      this.show_failed = false;
      this.show_spinner = true;

      let formData = new FormData();
      formData.append("contact", JSON.stringify(this.form));

      this.$http({
        method: "post",
        url: "http://127.0.0.1:5000/contact/",
        data: formData,
        headers: { "Content-Type": "multipart/form-data" }
      })
        .then(() => {
          this.show_spinner = false;
          this.show_success = true;
          this.onReset(evt);
        })
        .catch(() => {
          this.show_spinner = false;
          this.show_success = false;
          this.show_failed = true;
        });
    },
    onReset(evt) {
      evt.preventDefault();
      /* Reset our form values */
      this.form.email = "";
      this.form.name = "";
      this.form.comment = "";
      this.form.food = null;
      this.form.checked = [];
      /* Trick to reset/clear native browser form validation state */
      this.show = false;
      this.$nextTick(() => {
        this.show = true;
      });
    }
  }
};
</script>

<style scoped>
#align_center {
  margin: 0 auto;
}
</style>
